<template>
  <nav class="navbar navbar-default">
    <a class="navbar-brand" href="/">
      <i class="fa fa-cogs fa-lg"></i> &nbsp;
      {{$store.state.appTitle}}
    </a>
    <ul class="nav navbar-nav navbar-right">

      <!------------------------- Home page link ------------------------->
      <li>
        <router-link to="/" class="nav-item nav-link">
          <span v-translate>Home</span>
        </router-link>
      </li>

      <!---------------------------- Lang Menu --------------------------->
      <li class="dropdown">
        <a class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">
          <span v-translate>Lang</span>
          <span class="caret"></span>
        </a>
        <ul class="dropdown-menu">
          <li v-for="lang in langs"  :class="lang.abbr == $translate.current ? 'active' : ''">
            <a class="nav-item nav-link" @click="changeLang(lang.abbr)">
              <span v-translate>{{lang.full}}</span>
            </a>
          </li>
        </ul>
      </li>

      <li>&nbsp;&nbsp;&nbsp;&nbsp;</li>
    </ul>
  </nav>
</template>

<script>
  export default {
    name: 'navbar',
    data () {
      return {
        langs: [
          { abbr: 'en', full: 'English' },
          { abbr: 'fr', full: 'French' },
          { abbr: 'es', full: 'Spannish' }
        ]
      }
    },
    methods: {
      changeLang (lang) {
        this.$translate.setLang(lang)
      }
    },
    locales: {
      fr: {
        'Home': 'Accueil',
        'Lang': 'Langue',
        'English': 'Anglais',
        'French': 'Français',
        'Spannish': 'Espagnol'
      },
      es: {
        'Home': 'Inicio',
        'Lang': 'Idioma',
        'English': 'Inglés',
        'French': 'Francés',
        'Spannish': 'Español'
      }
    }
  }
</script>

<style scoped>
  .active-link {
    font-weight: bold;
  }
</style>



